import React from 'react'
import "./AboutStyles.css";
import logo from "../Assets/about-picture.png";

import './Numberof.css';

export default function About() {
  return (
    <div className='about-container' id='about'>
        <div className='about-heading'>
            <p className='my-about'>About <span>Me</span></p>
        </div>
        <div className='about-img-div'>
            <div className='about-img-out'>
                <div className='about-img'>
                    <img src={logo} alt='logo' />
                </div>
            </div>
        </div>
        <div className='about-content'>
            <div className='about-dec'>
                <p>
                Hi, I am Yatharth. I live to learn. I enjoy creating things that live on the internet.
                <br></br>
                I hold a Bachelors degree in Computer Science and started my career as an Associate Application Developer at Unisys
                <br></br>
                To expand my skill set, I pursued advanced studies in Artificial Intelligence and Cloud Computing.
                <br></br>
                I seek new challenges and try to think out-of-the-box while looking for creative solutions to a given problem.
                </p>
            </div>
        </div>
    </div>
  )
}
