import React from 'react'
import Typewriter from "typewriter-effect";
import { AiFillLinkedin, AiFillFacebook, AiFillTwitterCircle,AiFillGithub } from "react-icons/ai";
// import heroImage from "../Assets/picofme (1).png";
import heroImage from "../Assets/profile-without-bg.png"
import "../Component/HeroStyles.css";

export default function Hero() {
    const links = [
        {
          to: '//www.linkedin.com/in/yatharth-thakur',
          icon: <AiFillLinkedin />
        },{
          to: '//https://www.facebook.com/',
          icon: <AiFillFacebook />
        },{
          to: '//twitter.com/yatharththakur8',
          icon: <AiFillTwitterCircle />
        },
        {
          to: 'https://github.com/Yatharth08',
          icon: <AiFillGithub/>
        }
      ];
    
    const handleHireMeClick = () => {
      const email = "thakuryatharth1@gmail.com"; 
      const subject = "Hiring Inquiry";
      const body = "Hello Yatharth,\n\nI would like to discuss an opportunity with you.";
    
      // Open default email app
      const mailtoLink = `mailto:${email}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
      window.open(mailtoLink, "_blank");
    };
      
  return (
    <div>
      <div className='hero-section' id='hero'>
        <div className='container'>
            <div className='container-left'>
                <div className='container-heading'>
                    <div className="hello">
                        <Typewriter
                        options={{
                          loop:true,
                        }}
                        onInit={(typewriter)=> { 
                        typewriter
                        .typeString("Hello World")
                        .pauseFor(1000)
                        .deleteAll()
                        .typeString("I am Yatharth Thakur")
                        .pauseFor(2000)
                        .start();
                        }}
                        />
                    </div>
                    <br/>
                    <div className='basic-info'>
                      <p>I'm a Developer, Innovator and an Engineer!
                        <br/>Seeking a co-op position at an organization
                      </p>
                    </div>
                    <div>
                        <button id = 'hire-me-btn' onClick={handleHireMeClick}>Hire me</button>
                    </div>
                    <div className='icon-list'>
                      {links.map((link)=>
                          {
                              console.log(link.to)
                              return(
                                  <a href={link.to} target = "_blank" className="nav-link" rel="noreferrer">
                                    <span className="icon">{link.icon}</span>
                                  </a>
                              );
                          })
                      }
                    </div>
                </div>
            </div>
            <div className='container-right'>
              <div className='hero-img-out'>
                <img src={heroImage} alt='hero'></img>
              </div>
            </div>   
        </div>
      </div>
    </div>
  )
}
