import React from 'react'
import "../Component/ProjectStyles.css";

export default function Project({data}) {    
    return (
    <div>
        <a href={data.link} target = "_blank" rel="noreferrer" className='project-link'>
            <div className='project-box'>
                <div className='main'>
                    <img className={"img-fluid"} src={`./assets_cart/${data.thumbnail}`} alt={data.title}/>
                    <h4 className='hover-description'>{data.description}</h4>
                    <div className='view-btn-div'>
                        <button id='button-view'>View</button>
                    </div>
                </div>
            </div>
        </a>
    </div>
    )
}
