import React from 'react'
import "./ContactStyles.css";
import { AiFillLinkedin, AiFillFacebook, AiFillTwitterCircle, AiFillGithub, AiOutlineCopyright } from "react-icons/ai";
import "./Footer.css";
export default function Contact() {
  const links = 
  [
    {
      to: "https://www.linkedin.com/in/yatharth-thakur",
      icon: <AiFillLinkedin />
    },
    {
      to: "https://www.facebook.com",
      icon: <AiFillFacebook />
    },
    {
      to: "https://www.twitter.com",
      icon: <AiFillTwitterCircle />
    },
    {
      to: "https://www.github.com",
      icon: <AiFillGithub />
    }
  ];

  return (
    <div id='contact' className='contact-main'>
        <div className='contact-heading' >
            <p className='my-contact'>Contact <span>Me!</span></p>
        </div>

        <div className='contact-form'>
          <div>
            <form action='https://formspree.io/f/xzbwnrry' method='POST'>
              <div className='name-fields'>
                <input id='first-name' type='text' name="first_name" placeholder='First Name' autoComplete='off' required />
                <input id='last-name' type='text' name="last_name" placeholder='Last Name' autoComplete='off' required />
              </div>
              <input id='email' type='email' name = "email" placeholder='Email' autoComplete='off' required></input>
              <textarea id='message' name="message" placeholder='Message' autoComplete='off' required></textarea>
              <div>
                <button id = "btn-submit" type="submit" value="send">Submit</button>
              </div>
            </form>
          </div>
          <br></br>
        </div> 

        <div className="footer-container">
          <hr />
            <div className="footer-logo">Yatharth.</div>
              <div className="footer-info">
                {links.map((link, index) => (
                  <a
                  key={index}
                  href={link.to}
                  className="nav-link"
                  target="_blank"
                  rel="noopener noreferrer"
                  >
                    <span className="icon">{link.icon}</span>
                  </a>
                ))}
              </div>
              <p>
                <span className="copyright"><AiOutlineCopyright /></span>
                <span className='res'>Copyright Yatharth Thakur | All rights reserved</span>
              </p>
            </div>
    </div>
  )
}
