import React, { useState, useEffect } from "react";
import "../Component/NavbarStyles.css";
import { Link } from "react-scroll";

export default function Navbar() {
  const [navColor, setNavColor] = useState("#0a192f"); // Default color
  const [isOpen, setIsOpen] = useState(false); // State for dropdown menu

  useEffect(() => {
    const sections = document.querySelectorAll("section");

    const getThreshold = (id) => {
      if (window.innerWidth <= 768) { // Mobile View
        return {
          about: 0.8,
          resume: 0.4,
          work: 0.43,
          Certificates: 0.35,
          contact: 0.6,
        }[id] || 0.5; // Default threshold
      } else { // Desktop View
        return {
          about: 0.8,
          resume: 0.9,
          work: 0.7,
          Certificates: 0.9,
          contact: 0.6,
        }[id] || 0.7; // Default threshold
      }
    };

    const observers = [];

    sections.forEach((section) => {
      const threshold = getThreshold(section.id);
      const observer = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting) {
              if (entry.target.id === "about" || entry.target.id === "work" || entry.target.id === "contact") {
                setNavColor("#10223e"); // Change navbar color
              } else {
                setNavColor("#0a192f"); // Default navbar color
              }
            }
          });
        },
        { root: null, threshold: threshold }
      );

      observer.observe(section);
      observers.push(observer);
    });

    return () => {
      observers.forEach((observer) => observer.disconnect());
    };
  }, []);

  return (
    <nav style={{ backgroundColor: navColor }}>
      <div className="logo">Yatharth.</div>

      {/* Main Navbar Links */}
      <ul className="desktop-menu">
        <li><Link to="home" smooth={true} duration={500}>Home</Link></li>
        <li><Link to="about" smooth={true} duration={500}>About</Link></li>
        <li><Link to="resume" smooth={true} duration={500}>Resume</Link></li>
        <li><Link to="work" smooth={true} duration={500}>Work</Link></li>
        <li><Link to="Certificates" smooth={true} duration={500}>Certificates</Link></li>
        <li><Link to="contact" smooth={true} duration={500}>Contact</Link></li>
      </ul>

      {/* Hamburger Menu for Mobile */}
      <div className="hamburger" onClick={() => setIsOpen(!isOpen)}>
        <span className={isOpen ? "bar open" : "bar"}></span>
        <span className={isOpen ? "bar open" : "bar"}></span>
        <span className={isOpen ? "bar open" : "bar"}></span>
      </div>

      {/* Dropdown Menu */}
      <ul
        className={`dropdown-menu ${isOpen ? "open" : ""}`}
        style={{ backgroundColor: navColor }}
      >
        <li><Link to="home" smooth={true} duration={500} onClick={() => setIsOpen(false)}>Home</Link></li>
        <li><Link to="about" smooth={true} duration={500} onClick={() => setIsOpen(false)}>About</Link></li>
        <li><Link to="resume" smooth={true} duration={500} onClick={() => setIsOpen(false)}>Resume</Link></li>
        <li><Link to="work" smooth={true} duration={500} onClick={() => setIsOpen(false)}>Work</Link></li>
        <li><Link to="Certificates" smooth={true} duration={500} onClick={() => setIsOpen(false)}>Certificates</Link></li>
        <li><Link to="contact" smooth={true} duration={500} onClick={() => setIsOpen(false)}>Contact</Link></li>
      </ul>
    </nav>
  );
}
